import { Directive, HostListener, ElementRef, Input, Renderer2 } from '@angular/core';

@Directive({
  selector: '[appStringValidation]'
})
export class StringValidationDirective {
  // Allow decimal numbers and negative values
  // private regex: RegExp = new RegExp(/^[a-zA-Z\s]*$/g);
  // // Allow key codes for special events. Reflect :
  // // Backspace, tab, end, home
  // private specialKeys: Array<string> = ['Backspace', 'Tab', 'End', 'Home', '-'];

  // constructor(private el: ElementRef) {
  // }
  // @HostListener('keydown', ['$event'])
  // onKeyDown(event: KeyboardEvent) {
  //   // Allow Backspace, tab, end, and home keys
  //   if (this.specialKeys.indexOf(event.key) !== -1) {
  //     return;
  //   }
  //   const current: string = this.el.nativeElement.value;
  //   const next: string = current.concat(event.key);
  //   if (next && !String(next).match(this.regex)) {
  //     event.preventDefault();
  //   }
  // }
  @Input('appStringValidation') inputType: string;

showMsg = false;
pattern: RegExp;

private regexMap = { // add your own
integer: /^[0-9 ]*$/g,
float: /^-?[0-9]+(\.[0-9]*){0,1}$/g,
// float:/^(?=.+)(?:[1-9]\d*|0)?(?:\.\d+)?$/g,
words: /^[a-zA-Z\s]*$/g,
point25: /^\-?[0-9]*(?:\\.25|\\.50|\\.75|)$/g,
badBoys: /^[^{}*+£$%\\^-_]+$/g,
mobile:/^[0-9]{10,10}$/
};

constructor(public el: ElementRef, public renderer: Renderer2) { }

@HostListener('keypress', ['$event']) onInput(e) {
this.pattern = this.regexMap[this.inputType];
const inputChar = e.key;
this.pattern.lastIndex = 0; // dont know why but had to add this

if (this.pattern.test(inputChar)) {
   // success
  this.renderer.setStyle(this.el.nativeElement, 'color', 'green');
  this.badBoyAlert('black');
} else {

  this.badBoyAlert('black');

  this.renderer.setStyle(this.el.nativeElement, 'color', 'red');
  e.preventDefault();

}

  }
  badBoyAlert(color: string) {
    setTimeout(() => {
      this.showMsg = true;
      this.renderer.setStyle(this.el.nativeElement, 'color', color);
    }, 2000);
  }

}
