import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FoundryComponent } from './foundry.component';
import { FoundryRoutingModule } from './foundry-routing.module';
import { LayoutModule } from '@angular/cdk/layout';
import { ControlMessagesComponent } from './validation/control-message-component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { NumberValidationDirective } from './validation/number-validation.directive';
import { StringValidationDirective } from './validation/string-validation.directive';

@NgModule({
  declarations: [FoundryComponent, ControlMessagesComponent, NumberValidationDirective, StringValidationDirective],
  imports: [
    CommonModule, FoundryRoutingModule, LayoutModule, FormsModule, ReactiveFormsModule
  ],
  exports: [ControlMessagesComponent, NumberValidationDirective, StringValidationDirective]
})
export class FoundryModule { }
